// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import {
    GoogleAuthProvider,
    getAuth,
    signInWithPopup,
    signInWithEmailAndPassword,
    createUserWithEmailAndPassword,
    sendPasswordResetEmail,
    signOut,
 } from "firebase/auth";
import {
    getFirestore,
    query,
    getDocs,
    collection,
    where,
    addDoc} from "firebase/firestore";
// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyC0x3a3aiJhuHCQa-yBCPmZXEyfMoypqZM",
  authDomain: "getting-married-8d590.firebaseapp.com",
  projectId: "getting-married-8d590",
  storageBucket: "getting-married-8d590.appspot.com",
  messagingSenderId: "252702346568",
  appId: "1:252702346568:web:66d649563ea16391bbf77d"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);
const db = getFirestore(app);

const googleProvider = new GoogleAuthProvider();
const signInWithGoogle = async () => {
    try {
        const res = await signInWithPopup(auth, googleProvider);
        const user = res.user;
        const userQuery = query(
            collection(db, "users"), 
            where("uid" == user.uid)
        );
        const docs = await getDocs(userQuery);

        if(docs.docs.length === 0){
            await addDoc(collection(db, "users"), {
                uid: user.uid,
                name: user.displayName,
                authProvider: "google",
                email: user.email,
            });
        }
    } 
    catch (err) {
        console.log(err);
    }
};

const logInWithEmailAndPassword = async (email, password) => {
    try {
        await signInWithEmailAndPassword(auth, email, password);
    } 
    catch (err) {
        console.log(err);
    }
}

const registerWithEmailAndPassword = async(name, email, password) =>{
    try {
        const res = await createUserWithEmailAndPassword(auth, email, password);
        const user = res.user;
        await addDoc(collection(db, "users"), {
            uid: user.uid,
            name,
            authProvider: "local",
            email,
        });
    } 
    catch (err) {
        console.log(err);
    }
}

const sendPasswordReset = async(email) =>{
    try {
        await sendPasswordResetEmail(auth, email);
        alert("Password reset link sent!");
    } 
    catch(err){
        console.log(err);
    }
}

const logout = () =>{
    signOut(auth);
}

export {
    auth,
    db,
    signInWithGoogle,
    logInWithEmailAndPassword,
    registerWithEmailAndPassword,
    sendPasswordReset,
    logout
}